}

.App-logo {
  height: 29vmin;
  /* pointer-events: none; */
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    transition: 1s;
    -webkit-animation: App-logo-spin infinite 0.5s linear;
