.App-header {
  background-color: #000;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.surface {
  height: 500px;
  width: 1000px;
  /* perspective: 500px; */
  margin: 20px 100px 0px 100px;
  display: inline-block;
  position: relative;
  background-color: #f7f3ce;
  box-shadow: 1px 1px 10px 10px lemonchiffon;
  -webkit-animation: rotate-surface 3s infinite ease-in-out alternate;
          animation: rotate-surface 3s infinite ease-in-out alternate;
  transition: 1s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.surface::before {
  content: "";
  position: absolute;
  background-color: #f7f3ce;
  top: 0;
  left: 0;
  margin: 20px 100px 0px 100px;
  width: 800px;
  height: 100px;
  -webkit-perspective: 500px;
          perspective: 500px;
}

@-webkit-keyframes rotate-surface {
  0% {
    -webkit-transform: rotateX(-10deg) rotateY(-10deg) rotateZ(-2deg);
            transform: rotateX(-10deg) rotateY(-10deg) rotateZ(-2deg);
  }

  100% {
    -webkit-transform: rotateX(10deg) rotateY(10deg) rotateZ(2deg);
            transform: rotateX(10deg) rotateY(10deg) rotateZ(2deg);
  }
}

@keyframes rotate-surface {
  0% {
    -webkit-transform: rotateX(-10deg) rotateY(-10deg) rotateZ(-2deg);
            transform: rotateX(-10deg) rotateY(-10deg) rotateZ(-2deg);
  }

  100% {
    -webkit-transform: rotateX(10deg) rotateY(10deg) rotateZ(2deg);
            transform: rotateX(10deg) rotateY(10deg) rotateZ(2deg);
  }
}

.surface:hover {
  transition: 1s;
  box-shadow: 2px 2px 20px 20px lemonchiffon;
}

@media only screen and (max-width: 900px) {
}

@media screen and (max-width: 992px) {
  .App-header {
    transition: 1s;
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
  }
}

