.CS50 {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: #000;
}

h1 {
  font-size: 4em;
  color: #fff;
}

.typing {
  position: relative;
}

.typing::after {
  content: "|";
  position: absolute;
  right: 0;
  width: 100%;
  color: white;
  background: #000;
  -webkit-animation: typing 1s steps(19) forwards, caret 2s infinite;
          animation: typing 1s steps(19) forwards, caret 2s infinite;
}

@-webkit-keyframes typing {
  to {
    width: 0;
  }
}

@keyframes typing {
  to {
    width: 0;
  }
}

@-webkit-keyframes caret {
  50% {
    color: transparent;
  }
}

@keyframes caret {
  50% {
    color: transparent;
  }
}
